<template>
  <div>
    <div class="top-nav fixed-top bg-white pt-2 pb-2">
      <div class="row m-0">
        <div class="col p-0">
          <a href="javascript:;" class="btn btn-block btn-sm" v-scroll-to="{el: '#profile', offset: -70}">
            <i class="mdi mdi-account"></i>
            <div>프로필</div>
          </a>          
        </div>
        <div class="col p-0">
          <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#experience', offset: -70}">
            <i class="mdi mdi-star"></i>
            <div>경력사항</div>
          </a>          
        </div>
        <div class="col p-0">
          <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#certificate', offset: -70}">
            <i class="mdi mdi-license"></i>
            <div>자격증</div>
          </a>          
        </div>
        <div class="col p-0">
          <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#award', offset: -70}">
            <i class="mdi mdi-trophy"></i>
            <div>수상내역</div>
          </a>          
        </div>
         <div class="col p-0">
          <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#ovstraining', offset: -70}">
            <i class="mdi mdi-leaf"></i>
            <div>대외활동</div>
          </a>          
        </div>        
      </div>
    </div>
    <div class="card mb-3" id="profile" style="margin-top: 63px;">
      <div class="image-wrapper">
        <div class="image" style="background-image: url(@/assets/images/profile.jpg);"></div>
      </div>
      <div class="card-body">
        <h3 class="text-center mb-3">MinGyeong.Choi</h3>
        <div class="job-desc mb-3">teacher</div>
        <div class="social-btns">
          <a href="javascript:;">
            <i class="mdi mdi-instagram"></i>
          </a>
          <a class="ml-3 mr-3" href="javascript:;">
            <i class="mdi mdi-facebook"></i>
          </a>
          <a href="javascript:;">
            <i class="mdi mdi-twitter"></i>
          </a>
        </div>
      </div>

      <div class="card-body pl-5 pr-5">
        <div class="mb-3">
          <span v-html="profile.intro"></span>
        </div>
        <ul class="personal-info p-0">
          <li>
            <strong class="float-left bg-danger text-white">
              Name
            </strong>
            <span>{{ profile.name }}</span>
          </li>
          <li>
            <strong class="float-left bg-danger text-white">
              Birth
            </strong>
            <span>{{ profile.birth }}</span>
          </li>
          <li>
            <strong class="float-left bg-danger text-white">
              Address
            </strong>
            <span>{{ profile.address }}</span>
          </li>
          <li>
            <strong class="float-left bg-danger text-white">
              Email
            </strong>
            <span>{{ profile.email }}</span>
          </li>
        </ul>
        <div class="clearfix"></div>
      </div>

      <div class="card-footer d-block bg-white">
        <a href="javascript:;" class="btn btn-block">
          CONTACT ME
          <i class="mdi mdi-send-outline"></i>
        </a>
      </div>
    </div>    

    <div class="card mb-3" id="experience">
      <div class="card-header">
        <h4>경력사항</h4>
      </div>
      <div class="card-body pl-5 pr-5">
        <timeline :items="experience"/>
      </div>
    </div>

    <div class="card mb-3" id="certificate">
      <div class="card-header">
        <h4>자격증</h4>
      </div>
      <div class="card-body pl-5 pr-5">
        <div class="mb-2" v-for="(item, index) of certificate" :key="index">
          <div>
            <i class="mdi mdi-license"></i> {{ item.name }}
          </div>
          <div class="pl-3">
            <small class="text-secondary"> {{ item.date }} {{ item.company }}</small>
          </div>
        </div>        
      </div>
    </div>

    <div class="card mb-3" id="award">
      <div class="card-header">
        <h4>수상내역</h4>
      </div>
      <div class="card-body pl-5 pr-5">
         <div class="mb-3" v-for="(item, index) of award" :key="index">
          <i class="mdi mdi-trophy"></i>
          {{ item.name }}
          <small class="text-secondary">
            <strong>{{ item.company }}</strong>
            {{ item.date }}
          </small>
        </div>
      </div>
    </div>

    <div class="card mb-3" id="ovstraining">
      <div class="card-header">
        <h4>대외활동</h4>
      </div>
      <div class="card-body pl-5 pr-5">
        <div class="mb-3" v-for="(item, index) of ovstraining" :key="index">
          <div>
            <strong>&middot; {{ item.title }}</strong>
            <small>{{ item.date }}</small>
          </div>                  
          <div v-html="item.desc"></div> 
        </div>                
      </div>
    </div>

  </div>
</template>

<script>
import Timeline from './ui/Timeline'
import pageData from '@/assets/data'
export default {
  name: 'MobileLayout',
  components: {
    Timeline
  },
  data () {
    return {
      profile: pageData.profile,
      education: pageData.education,
      experience: pageData.experience,
      certificate: pageData.certificate,
      award: pageData.award,
      ovstraining: pageData.ovstraining
    }
  }
}
</script>

<style lang="scss" scoped>
.top-nav{
  box-shadow: 0 0 1px rgba(0, 0, 0, .5);
  .btn:focus{
    box-shadow: none;
    opacity: .8;
  }
}
.vl-line{
  border-left: 1px solid #efefef;
}
.image-wrapper{
  position: relative;
  height: 350px;
  .image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 0;
    overflow: hidden;
    &:before,
    &:after{
      content: '';
        position: absolute;
        left: -25%;
        bottom: -63%;
        width: 100%;
        height: 70%;
        background: #fff;
        transform: rotate(12deg);
        -webkit-transform: rotate(12deg);
        -moz-transform: rotate(12deg);
        -o-transform: rotate(12deg);
    }
    &:after{
      left: auto;
      right: -25%;
      transform: rotate(-8deg);
      -webkit-transform: rotate(-8deg);
    }
  }
}

.card-header{
  text-align: center;
  background-color: white;
  padding: 1.5rem 0;
  h4{
    margin: 0;
  }
}

.job-desc{
  color: #f54134;
  text-align: center;
}
.social-btns{
  text-align: center;
  a, a:hover{
    color: inherit;        
  }
}

.personal-info{      
  li{
    position: relative;
    padding: 11px 0;        
    display: block;
    vertical-align: top;
    width: auto;
    font-size: .8rem;
    word-break: break-word;
    text-align: right;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: -moz-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: -webkit-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: radial-gradient(ellipse at center,#ddd 0%,rgba(255,255,255,0) 70%);       
    }
    strong{
      padding: 1px 8px;
    }
  }      
}
</style>


